import React, { useEffect, useState } from 'react';
import Axios from 'axios';

import SearchBar from '../../components/SearchBar/SearchBar';
import GifGrid from '../../components/GifGrid/GifGrid';
import Layout from '../../components/Layout/Layout';
import Waiting from '../../components/Waiting/Waiting';

const GifContainer = () => {
    const [searchQuery, setSearchQuery] = useState('');
    const [loadedGifs, setLoadedGifs] = useState(null);
    const [firstLoad, setFirsLoad] = useState(true);

    const searchHandler = (updatedSearchQuery) => {
        setSearchQuery(updatedSearchQuery);
    }

    const fetchSearchGifs = async (search) => {
        try {
            const response = await Axios.get('https://api.tenor.com/v1/search?key=DWOMV8H8CC5W&limit=50&media_filter=minimal&q=' + search);
            const gifs = response.data.results.map((el) => {
                return {
                    id: el.id,
                    gifUrl: el.media[0].gif.url,
                    gifPreviewUrl: el.media[0].gif.preview,
                }
            });
            setLoadedGifs(gifs);
        }
        catch (err) {
            console.log(err);
        }
    }

    // componentDidUpdate -> (searchQuery)
    useEffect(() => {
        if (searchQuery) {
            fetchSearchGifs(searchQuery);
            setLoadedGifs(null);
            setFirsLoad(false);
        }
    }, [searchQuery])

    return (
        <Layout>
            <SearchBar search={searchQuery} callback={searchHandler} />
            { (firstLoad) ? <Waiting/> : <GifGrid gifs={loadedGifs} />}
        </Layout>
    );

}

export default GifContainer;