import React, { useEffect, useState } from 'react';
import Axios from 'axios';

import GifGrid from '../../components/GifGrid/GifGrid';
import Layout from '../../components/Layout/Layout';

const GifContainer = () => {
    const [loadedGifs, setLoadedGifs] = useState(null);

    const fetchTrendingGifs = async () => {
        try {
            const response = await Axios.get('https://api.tenor.com/v1/trending?key=DWOMV8H8CC5W&media_filter=minimal&limit=50');
            const gifs = response.data.results.map((el) => {
                return {
                    id: el.id,
                    gifUrl: el.media[0].gif.url,
                    gifPreviewUrl: el.media[0].gif.preview,
                }
            });
            setLoadedGifs(gifs);
        }
        catch (err) {
            console.error(err);
        }
    }

    // componentDidMount -> fetching initial gifs
    useEffect(() => {
        fetchTrendingGifs();
    }, []);

    return (
        <Layout>
            <GifGrid gifs={loadedGifs} />
        </Layout>
    );

}

export default GifContainer;