import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

import Logo from '../Logo/Logo';
import styles from './Navbar.module.css';
import Layout from '../Layout/Layout';

const Navbar = () => {
    const [showSidebar, setShowSidebar] = useState(false);

    const toggleSideBar = () => {
        setShowSidebar((perv) => {
            return !perv;
        })
    }

    const sidebarClasses = [styles.sidebar];
    const backdropClasses = [styles.backdrop];

    if (showSidebar) {
        sidebarClasses.push(styles.active);
        backdropClasses.push(styles.visible);
    }

    return (
        <>
            <header className={styles.navbar}>
                <div className={styles.container}>
                    <Logo />
                    <div className={styles.navlinks}>
                        <ul>
                            <li>
                                <NavLink exact to="/react-gif-finder" activeClassName={styles.active}>Home</NavLink>
                            </li>
                            <li>
                                <NavLink to="/react-gif-finder/trending" activeClassName={styles.active}>Trending</NavLink>
                            </li>
                            <li>
                                <NavLink to="/react-gif-finder/search" activeClassName={styles.active}>Search</NavLink>
                            </li>
                        </ul>
                    </div>
                </div>
            </header>
            <button className={styles.toggle} onClick={toggleSideBar}>Menu</button>
            <div className={backdropClasses.join(' ')} onClick={toggleSideBar}></div>
            <div className={sidebarClasses.join(' ')}>
                <ul>
                    <li>
                        <NavLink exact to="/react-gif-finder" activeClassName={styles.active} onClick={toggleSideBar}>Home</NavLink>
                    </li>
                    <li>
                        <NavLink to="/react-gif-finder/trending" activeClassName={styles.active} onClick={toggleSideBar}>Trending</NavLink>
                    </li>
                    <li>
                        <NavLink to="/react-gif-finder/search" activeClassName={styles.active} onClick={toggleSideBar}>Search</NavLink>
                    </li>
                </ul>
            </div>
        </>
    );
}

export default Navbar;