import React from 'react';
import { NavLink } from 'react-router-dom';

import Layout from "../../components/Layout/Layout";

import styles from "./Home.module.css";

const Home = () => {
    return (
        <Layout>
            <div className={styles.showcase}>
                <h1>Find Amazing Gifs</h1>
                <p>Made with Tenor Api</p>
                <div>
                    <NavLink className={[styles.button,styles.solid].join(' ')} to="/react-gif-finder/search">Search</NavLink>
                    <NavLink className={[styles.button,styles.outline].join(' ')} to="/react-gif-finder/trending">Trending</NavLink>
                </div>
            </div>
        </Layout>
    );

}

export default Home;